.portfolio-item {
    box-sizing: border-box;
    flex-basis: 33%;
    text-decoration: none;
    color: inherit;
    display: block;
    margin: 10px;
}

.portfolio-item img {
    max-width: 100%;
}

.portfolio-item__title {
    margin: 10px 0;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
}